<template>
  <div class="blog-page">
    <el-container class="blog-container">
      <el-header class="blog-header" height="80px">
        <BaseHeader />
      </el-header>
      <el-container class="blog-middle">
        <el-row
          class="blog-middle--wrap"
          type="flex"
          justify="flex-wrap"
          align="center"
        >
          <el-col :span="6" class="hidden-md-and-down">
            <el-aside width="" class="blog-aside">
              <BaseAside />
            </el-aside>
          </el-col>
          <el-col :span="16" :sm="24" :xs="24">
            <el-main class="blog-main">
              <vue-scroll
                v-if="reloadView"
                @handle-scroll="loadMore"
                ref="scroll"
              >
                <!-- <keep-alive :include="['VueBlogArticleList']">
                  <router-view
                    :isLoading="isLoading"
                    :scrollTop="scrollTop"
                  ></router-view>
                </keep-alive> -->
                <router-view
                  style="margin: 15px 20px"
                  v-if="reloadView"
                  :isLoading="isLoading"
                ></router-view>
              </vue-scroll>
            </el-main>
          </el-col>
          <el-col :span="2" class="hidden-md-and-down">
            <CircleMenu />
          </el-col>
        </el-row>
      </el-container>
      <!-- <el-footer class="blog-footer" height="100px">Footer</el-footer> -->
    </el-container>

    <!-- dialog/modal -->
    <BaseModal />
    <!-- live2d -->
    <!-- <Live2D class="hidden-md-and-down" /> -->
  </div>
</template>

<script>
import BaseModal from '@/components/base/BaseModal.vue'
import BaseHeader from '@/components/base/BaseHeader.vue'
import BaseAside from '@/components/base/BaseAside.vue'
import CircleMenu from '@/components/base/BaseCircleMenu.vue'
//import Live2D from '@/components/live2d.vue'
import { throttle } from 'lodash'
export default {
  name: 'VueBlogHome',
  components: { BaseModal, BaseHeader, BaseAside, CircleMenu },
  provide() {
    return {
      switchLoading: this.switchLoading,
      scrollTo: this.scrollTo
    }
  },
  data() {
    return {
      theme: {
        ColorOlive: '#60dda5',
        BgcLight: '#fafafa',
        BgcMenu: '#3D404D'
      },
      isLoading: false,
      reloadView: true,
      scrollTop: 0
    }
  },
  mounted() {
    //Live2D 使用CDN引入成品并加响应式(mid以下隐藏)
    let live2d = document.querySelector('#waifu')
    this.$nextTick(() => {
      live2d && live2d.classList.add('hidden-md-and-down')
    })
  },
  //每次刷新页面重载main的router-view scroll重置
  watch: {
    $route() {
      this.reload()
    }
  },
  methods: {
    reload() {
      this.reloadView = false
      this.$nextTick(() => {
        this.reloadView = true
      })
    },
    ///////////////////////////vuescroll部分
    switchLoading() {
      this.isLoading = false
    },
    scrollTo(scrollTop) {
      //(第二个参数为动画时间,第三个参数为动画参数)
      this.$refs['scroll'].scrollTo({ y: scrollTop }, 0, 'easeInCubic')
    },
    //vertical 和 horizontal垂直和水平滚动条的信息。第三个参数是触发滚动的原生事件。
    // .process可以确定滚动进度(%)
    loadMore:
      //lodash节流函数
      throttle(function (vertical) {
        //记录滚动高度
        //this.scrollTop = this.$refs['scroll'].getPosition().scrollTop

        //加载中 以及大于最大页数时,不执行
        if (this.isLoading) return false
        if (vertical.process >= 0.998) {
          this.isLoading = true
        }
      }, 400)
  }
}
</script>

<style lang="stylus">
.blog-container
  overflow hidden
  height 100%
  background-color theme-bgcolor-light

.blog-middle
  height calc(100vh - 80px)

.blog-middle--wrap
  width 100%
  max-width 1440px
  margin 10px auto

.el-main, .el-aside
  height 100%

.el-aside.blog-aside
  padding 15px 10px 0 20px
  overflow hidden

.el-main.blog-main
  padding 0
  @media only screen and (max-width 767px)
    padding 20px

.el-header.blog-header
  padding 0

.blog-header, .blog-footer
  background-color theme-top-bottom-color

.blog-card--title
  font-size 24px
  font-weight bold
  padding 15px 0 30px
</style>
