export default {
  "home": [
    {
      route: '/editor',
      icon: "el-icon-edit-outline",
      tooltip: "写文章",
    }
  ],
  "articleList": [
    {
      route: '/editor',
      icon: "el-icon-edit-outline",
      tooltip: "写文章",
    }
  ],
  "column": [
    {
      icon: "el-icon-plus",
      tooltip: "新建分区",
      handler() {
        this.openModal('postColumn')
      }
    }
  ],
  "article": [
    {
      name: 'comments',
      icon: "el-icon-chat-round",
      tooltip: "评论",
      handler() {
        this.$bus.$emit('focusCommentEditor')
      }
    },
    {
      name: 'likes',
      icon: "el-icon-thumb",
      tooltip: "点赞",
      handler() {
        this.artLikesHandler()
      }
    },
    {
      name: 'favorites',
      icon: "el-icon-star-off",
      tooltip: "收藏",
    },
  ],
  "about": [
    {
      icon: "el-icon-back",
      tooltip: "返回主页",
      handler() {
        this.$router.push('/index')
      }
    }
  ],
}