<template>
  <transition
    enter-active-class="animate__animated animate__fadeIn animate__faster"
    appear
  >
    <div>
      <!-- 搜索结果页信息 -->
      <el-card v-if="isSearch">
        <el-page-header @back="refreshArticles" :content="searchMsg">
        </el-page-header>
      </el-card>
      <!-- 文章列表 -->
      <div v-if="articles.length" class="blog-article--list">
        <el-card
          v-for="article in articles"
          :key="article._id"
          class="card-article--item"
          shadow="always"
        >
          <ArticleItem :article="article" />
        </el-card>
      </div>
      <!-- 404 -->
      <div v-else class="blog-article--no">
        <h3 class="blog-article--title">很遗憾 没有找到对应的文章</h3>
        <el-button type="primary" @click="routeToEditor">去写一篇?</el-button>
      </div>
    </div>
  </transition>
</template>

<script>
import ArticleItem from '@/components/article/ArticleItem'
import QS from 'qs'
export default {
  name: 'VueBlogArticleList',
  components: { ArticleItem },
  inject: ['switchLoading', 'scrollTo'],
  props: {
    columnId: {
      type: String
    },
    isLoading: {
      type: Boolean
    }
    // scrollTop: {
    //   type: Number
    // }
  },
  data() {
    return {
      articles: [],
      //当前页
      page: 1,
      //每页数量
      size: 5,
      //总搜索结果数
      total: 0,
      //是否位于搜索结果页
      isSearch: false
      //currScrollTop: 0
    }
  },
  computed: {
    searchMsg() {
      return '搜索到 ' + this.total + ' 篇文章'
    }
  },
  //获取文章列表
  created() {
    this.getArticles()
  },
  mounted() {
    //搜索事件监听
    this.$bus.$on('headerSearch', (query) => {
      this.search = query
      this.isSearch = true
      this.cleanArticles()
      this.getArticles()
    })
  },
  // 废弃 -
  // //激活组件时 移动到上次滚动位置
  // activated() {
  //   console.log('activated')
  //   this.scrollTo(this.currScrollTop)
  // },
  // //组件失活,记录scrollTop
  // deactivated() {
  //   this.currScrollTop = this.scrollTop
  // },
  watch: {
    //监听父组件isLoading 如果变为true 加载文章
    isLoading(loadStat) {
      if (loadStat) {
        this.getArticles()
      }
    }
  },

  methods: {
    getQuery() {
      //如果是来自分区的请求
      let column = this.columnId || undefined
      //搜索请求
      let search = this.search || undefined
      //用于去除空键值
      let query = JSON.parse(
        JSON.stringify({
          column,
          search
        })
      )
      return query
    },
    async getArticles() {
      try {
        let data = { size: this.size, page: this.page }
        //监视路由是否携带query
        let query = this.getQuery()
        if (Object.entries(query).length > 0) {
          data.query = QS.stringify(query)
        }
        let result = await this.$http({ type: 'getArticles', data })
        //记录结果条数(用于搜索)
        this.total = result.total
        //如果已经完成了所有加载了 返回(终止循环锁)
        if (this.articles.length >= result.total) return
        //添加到页面
        this.articles.push(...result.list)
        //页数+1
        this.page++
        //重置加载状态
        this.switchLoading()
      } catch (err) {
        //console.log(err)
      }
    },
    //重置状态
    cleanArticles() {
      this.page = 1
      this.articles = []
    },
    //从搜索结果页返回主页
    refreshArticles() {
      this.search = undefined
      this.isSearch = false
      this.cleanArticles()
      this.getArticles()
    },
    routeToEditor() {
      this.$router.push({ name: 'editor', query: { columnId: this.columnId } })
    }
  },
  //销毁搜索监听事件(很重要)
  beforeDestroy() {
    this.$bus.$off('headerSearch')
  }
}
</script>

<style lang="stylus">
.card-article--item
  margin-bottom 10px

.blog-article--no
  display flex
  flex-direction column
  justify-content space-around
  align-items center
  height 50vh
  background-color #fff
  border-radius 4px
  box-shadow 0 2px 12px 0 rgba(0, 0, 0, 0.1)

.blog-article--title
  color theme-color
</style>