/*
mixin混入
  - 语法糖 写入 data methods computed 等属性方法
  - 引入组件内 使用 mixins: [IMPORT_MIXIN] 会将此mixin的属性方法与组件进行拼合(assign)
  - 组件存在同名属性方法 以组件优先
*/
export default {
  data() {
    return {
    };
  },
  methods: {
    openModal(type) {
      this.$store.dispatch('modal/openModal', type);
    },
  },
  computed: {

  },
}