<template>
  <article class="blog-article--item" v-if="article">
    <router-link :to="{ name: 'article', params: { id: article.id } }">
      <section class="blog-article--desc">
        <h3 class="blog-article--title">{{ article.title }}</h3>
        <p class="blog-article--author">{{ nickname }}</p>
        <div class="blog-article--info">
          <span><i class="el-icon-date" />发布于: {{ article.date }}</span>
          <span
            ><i class="el-icon-view" />已有 {{ article.hit_num }} 次浏览</span
          >
        </div>
        <img
          class="blog-article--cover"
          :src="article.cover"
          :alt="article.title"
          :title="article.title"
        />
      </section>
    </router-link>
    <p class="blog-article--intro">{{ content }}</p>
    <section class="blog-article--footer">
      <p class="blog-article--tags"><span>#标签1</span><span>#标签2</span></p>
      <p class="blog-article--func">
        <span class="article-func--like">点赞 ({{ article.like_num }})</span>
        <span class="article-func--comment"
          >评论 ({{ article.comment_num }})</span
        >
      </p>
    </section>
  </article>
</template>

<script>
export default {
  name: 'VueBlogArticleItem',
  props: {
    article: {
      type: Object
    }
  },
  data() {
    return {}
  },
  computed: {
    nickname() {
      return this.article?.author?.nickname
    },
    content() {
      //提取标签之间的内容
      return this.article?.content.replace(/<[^>]*>|/g, '').slice(0, 80) + '...'
    }
  },
  mounted() {},

  methods: {}
}
</script>

<style lang="stylus">
.blog-article--item, .blog-text-editor, .blog-article
  display flex
  flex-direction column
  .blog-article--cover
    width 50%
    height auto
    padding: padding-per * 2 0

.blog-article--desc
  display flex
  flex-direction column
  justify-content space-between
  align-items center
  &:hover *
    color darken(theme-highlight-color, 30%)

.blog-article--title
  font-size 24px
  font-weight bold
  text-align center

.blog-article--author
  line-height 2.5em

.blog-article--info, .blog-article--title, .blog-article--content
  width 100%

.blog-article--info
  display flex
  justify-content space-around
  padding padding-per 0
  @media screen and (max-width 1000px)
    flex-direction column
    align-items center

.blog-article--intro
  text-align justify
  text-indent 2em

.blog-article--footer
  display flex
  justify-content space-between
  padding padding-per 0
  & span
    cursor pointer
  & span:hover
    color darken(theme-highlight-color, 30%)

.blog-article--no
  justify-content space-around
  align-items center
  height 70vh
  .blog-btn
    width 60%
    border-radius bd-radius-per
</style>