<template>
  <div class="circle-menu" v-if="menuList.length">
    <transition-group name="menu" appear mode="out-in">
      <el-tooltip
        v-for="item in menuList"
        :key="item.tooltip"
        :content="item.tooltip"
        class="circle-menu--item"
        effect="dark"
        placement="right"
      >
        <el-button
          type="primary"
          :class="{ active: isLikeActive(item?.name) }"
          @click="clickHandler(item?.route, item?.handler)"
          :icon="item.icon"
          circle
        ></el-button>
      </el-tooltip>
    </transition-group>
  </div>
</template>

<script>
import MIXIN from '@/core/mixin'
import MIXIN_ART_LIKES from '@/core/mixin_artLikes'
import CircleMenuConfig from '@/configs/circleMenu.config'
export default {
  name: 'VueBlogBaseCircleMenu',
  mixins: [MIXIN, MIXIN_ART_LIKES],
  data() {
    return {
      menuName: 'home'
    }
  },
  created() {
    this.menuName = this.$route.name
  },
  //路由切换时记录menuName
  watch: {
    $route(to) {
      this.menuName = to.name
    }
  },
  computed: {
    menuList() {
      return CircleMenuConfig?.[this.menuName] || []
    }
  },
  methods: {
    hiddenAfterLogin(idx) {
      //某个按钮是否要在登录后隐藏 应该在config内设置 hiddenAfterLogin 属性为true
      if (CircleMenuConfig?.[this.menuName][idx]?.hiddenAfterLogin) {
        return this.isLogin
      }
      return false
    },
    //点赞按钮 如果非点赞按钮 return false 如果是点赞按钮执行相应功能
    isLikeActive(name) {
      if (name === 'likes') {
        return this.isLike(this.aid)
      }
      return false
    },
    //存在route跳转 || 存在handler执行句柄
    clickHandler(route, handler) {
      if (route) {
        this.$router.push(route)
        return
      }
      if (handler && typeof handler === 'function') {
        handler.call(this)
      }
    }
  }
}
</script>

<style lang="stylus" >
.circle-menu
  display flex
  flex-direction column
  justify-content flex-start
  padding-top 50px
  button
    width 60px
    height 60px
    margin 10px 30px 10px 10px
    font-size 32px
    box-shadow 0 2px 6px #999

.el-button.active, .el-button.active:hover, .el-button.active:focus
  background dodgerblue
  border-color dodgerblue

.menu-enter-active, .menu-leave-active, .menu-move
  transition 0.3s ease-in-out

// 必须
.menu-leave-active
  position absolute

.menu-enter, .menu-leave-to
  opacity 0

.menu-enter
  transform scale(0)
</style>