<template>
  <el-card class="card-aside" shadow="always">
    <h3 v-if="!$store.state.token" class="blog-aside--guest">
      您好,尊敬的游客
    </h3>
    <div v-else class="blog-aside--user">
      <div class="blog-avatar">
        <el-image
          class="blog-avatar--pic"
          v-if="userInfo.avatar"
          :src="userInfo.avatar"
          fit="cover"
        />
      </div>
      <div class="blog-info">
        <h2 class="blog-info--nickname">{{ userInfo.nickname }}</h2>
        <div class="blog-info-stat">
          <div class="info-stat--item">
            <a href="#">
              <span class="info-stat-name">文章</span>
              <span class="info-stat-count">{{ userInfo.article_num }}</span>
            </a>
          </div>
          <div class="info-stat--item">
            <a href="#">
              <span class="info-stat-name">评论</span>
              <span class="info-stat-count">{{ userInfo.comment_num }}</span>
            </a>
          </div>
        </div>
        <p class="info-user--sign">{{ userInfo.signature }}</p>
      </div>
    </div>
  </el-card>
</template>

<script>
export default {
  name: 'VueBlogBaseAside',

  data() {
    return {}
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo
    }
  }
}
</script>

<style lang="stylus">
.card-aside
  margin 0 auto
  text-align center

.blog-aside--guest
  font-size 24px

.blog-avatar
  padding: padding-per * 2 0

.blog-avatar--pic
  width 128px
  height @width
  box-shadow 0 2px 4px theme-bgcolor-dark
  border-radius 50%

.blog-info-stat
  display flex
  justify-content center
  align-items center

.info-stat--item
  position relative
  padding padding-per padding-per * 2
  &:nth-of-type(1)::after
    content ''
    position absolute
    top 0
    right 0
    width 1px
    height 100%
    transform scaleY(0.5)
    background-color theme-line-color

.info-stat--item span
  display block
</style>