<template>
  <div>
    <el-dialog
      :title="title"
      :width="width"
      :visible.sync="modalVisible"
      :before-close="modalBeforeClose"
    >
      <BaseForm
        v-if="formName"
        :formType="this.modalType"
        ref="form"
        @keydown.native.enter="submitForm"
      />
      <el-button
        v-if="hasSwitch"
        type="text"
        @click="switchModal(hasSwitch.switchTo)"
        >{{ hasSwitch.switchLabel }}</el-button
      >
      <div slot="footer" class="dialog-footer">
        <el-button
          v-for="btn in btns"
          :type="btn.type ?? ''"
          :key="btn.targetName"
          @click="btnHandler(btn.isSubmit)"
          >{{ btn.label }}</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import BaseForm from '@/components/base/BaseForm.vue'
import MODAL_CONFIG from '@/configs/modal.config'

import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('modal')
export default {
  name: 'VueBlogBaseModal',
  components: { BaseForm },
  data() {
    return {}
  },

  computed: {
    ...mapState(['modalVisible', 'modalType']),
    // 是否存在表单
    formName() {
      return MODAL_CONFIG[this.modalType]?.formName ?? false
    },
    // 是否要切换modal
    hasSwitch() {
      return MODAL_CONFIG[this.modalType]?.switch ?? false
    },
    // 是否要提交后刷新页面
    updateAfterSubmit() {
      return MODAL_CONFIG[this.modalType]?.updateAfterSubmit ?? false
    },
    title() {
      return MODAL_CONFIG[this.modalType]?.title ?? '默认标题'
    },
    width() {
      return MODAL_CONFIG[this.modalType]?.width ?? '40%'
    },
    btns() {
      return MODAL_CONFIG[this.modalType]?.btns ?? []
    }
  },
  methods: {
    modalBeforeClose() {
      //如果有表单,关闭的时候也要重置
      this.formName && this.resetForm()
      this.closeModal()
    },
    btnHandler(isSubmit = false) {
      //如果是提交请求HTTP 获取对应的form实例 校验执行
      if (isSubmit) {
        this.submitForm()
        return
      }
      this.resetForm()
    },
    submitForm() {
      const formRef = this.$refs['form']
      let formData = formRef.form
      // if (this.modalType === 'register') {
      //   formData = Object.fromEntries(
      //     Object.entries(formData).filter(([key]) => key !== 'password_confirm')
      //   )
      // }

      formRef.$refs['elForm'].validate(async (valid) => {
        if (!valid) return false
        try {
          await this.$http({
            type: this.modalType,
            data: formData
          })
          //需要更新视图的操作
          if (this.updateAfterSubmit) {
            this.$bus.$emit('updateView')
          }
          //关闭modal以及重置表单
          this.closeModal()
          this.resetForm()
        } catch (err) {
          //this.resetForm()
        }
      })
    },
    resetForm() {
      this.$refs['form'].resetForm()
    },
    ...mapActions(['closeModal', 'switchModal'])
  }
}
</script>

<style lang="stylus" ></style>