<template>
  <div>
    <h1>Vue博客</h1>
    <el-row type="flex" align="middle" class="blog-header--wrap">
      <!-- logo -->
      <el-col :xs="24" :sm="4" :lg="4">
        <router-link class="blog-logo" to="/" alt="Vue博客" title="Vue博客">
          <el-image
            class="blog-logo--img"
            style="width: 60px; height: 60px"
            :src="require('@/assets/logo.png')"
            fit="cover"
          ></el-image>
        </router-link>
      </el-col>
      <!-- 导航菜单 xs隐藏-->
      <el-col :lg="16" :sm="20" class="hidden-xs-only">
        <el-menu
          router
          ref="nav-menu"
          class="blog-nav"
          mode="horizontal"
          :background-color="theme.BgcMenu"
          :text-color="theme.BgcLight"
          :active-text-color="theme.ColorOlive"
          default-active="/index"
        >
          <el-menu-item index="/index">首页</el-menu-item>

          <el-menu-item index="/column">分类</el-menu-item>

          <el-menu-item index="/editor">写文章</el-menu-item>
          <el-menu-item index="/chat">聊天室</el-menu-item>
          <!-- 仅登录后显示 -->
          <el-menu-item v-if="$store.state.token" index="/user"
            >用户中心</el-menu-item
          >

          <el-menu-item index="/about">关于作者</el-menu-item>

          <el-submenu index="/">
            <template slot="title">友情链接</template>
            <a href="https://juejin.cn/" target="_blank">
              <el-menu-item> 稀土掘金 </el-menu-item>
            </a>
            <a href="https://cn.vuejs.org/" target="_blank">
              <el-menu-item> Vue.js </el-menu-item>
            </a>
            <a href="https://element.eleme.io/" target="_blank">
              <el-menu-item> ElementUI </el-menu-item>
            </a>
          </el-submenu>
        </el-menu>
      </el-col>
      <!-- 搜索栏 -->
      <el-col :span="5" class="blog-header--search hidden-md-and-down">
        <el-input
          class="header-search--wrap"
          placeholder="搜索文章.."
          v-model="searchVal"
          @keydown.native.enter="searchHandler"
        >
          <i
            slot="suffix"
            class="el-input__icon el-icon-search"
            @click="searchHandler"
            title="点击搜索"
          ></i>
        </el-input>
      </el-col>
      <!-- 用户中心 -->
      <el-col :span="3" class="hidden-sm-and-down">
        <div class="blog-header--user">
          <component :is="headUserComp"></component>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import UserLoginHeader from '@/components/user/UserLoginHeader.vue'
import UserCenterHeader from '@/components/user/UserCenterHeader.vue'

export default {
  name: 'VueBlogBaseHeader',
  components: { UserLoginHeader, UserCenterHeader },
  data() {
    return {
      theme: {
        ColorOlive: '#60dda5',
        BgcLight: '#fafafa',
        BgcMenu: '#3D404D'
      },
      searchVal: ''
    }
  },
  created() {
    //验证登录状态 已登录的情况下 获取当次会话的userInfo
    if (this.$store.state.token) {
      this.$store.dispatch('setUserInfo')
    }
  },
  watch: {
    //监听路由 确保导航栏激活指向正确 (通过refs注入data)
    $route(to) {
      this.$refs['nav-menu'].activeIndex = to.path
    }
  },
  computed: {
    headUserComp() {
      return this.$store.state.token ? 'UserCenterHeader' : 'UserLoginHeader'
    }
  },
  methods: {
    searchHandler() {
      this.$bus.$emit('headerSearch', this.searchVal)
      this.searchVal = ''
    }
  }
}
</script>

<style lang="stylus" >
.el-header
  padding 0

.blog-header h1
  position absolute
  text-indent -9999em

.blog-header--wrap
  height 80px
  max-width 1440px
  margin 0 auto

.blog-logo
  display block
  width 60px
  height 60px
  margin 0 auto

.el-menu-item
  height 80px

.blog-header--search
  display flex
  justify-content center
  .header-search--wrap
    max-width 200px
    & input:focus
      border none
      box-shadow 0 0 5px theme-color inset
  .el-icon-search
    cursor pointer
    &:hover
      color theme-color-orange
      transform scale(1.2)
</style>